import React from "react";
import { Button, CardContent, TextField } from "@material-ui/core";
import "../input.css";

const Input = ({ input, setInput, Findweather }) => {
  const handleChange = (event) => {
    event.preventDefault();
    setInput(event.target.value);
  };

  const handleClick = (event) => {
    event.preventDefault();
    Findweather();
  };

  return (
    <div>
      <div className="inputBox">
        <CardContent className="input_field">
          <h1>Apple Weather App</h1>
          <p>powered by</p>
          <img
            src="https://raw.githubusercontent.com/nandhahacker1/img/main/apple_logo-512%201.png"
            alt="logo"
          />
          <form>
            <TextField
              id="Input"
              label="Search Area"
              variant="filled"
              value={input}
              onChange={handleChange}
            />
            <br />
            <Button variant="contained" onClick={handleClick}>
              Search
            </Button>
          </form>
        </CardContent>
      </div>
    </div>
  );
};

export default Input;
