import React, { useState } from "react";
import axios from "axios";
import Input from "./components/input";
import Result from "./components/resulte";
import FlipMove from "react-flip-move";

const App = () => {
  const [input, setInput] = useState("");
  const [temp, setTemp] = useState("");
  const [icon, setIcon] = useState("");
  const [description, setDescription] = useState("");
  const [city, setCity] = useState("");

  const Findweather = async () => {
    const apiKey = "18ed9304b49a8e002f660c3a960811b3";
    const url =
      "https://api.openweathermap.org/data/2.5/weather?q=" +
      input +
      "&appid=" +
      apiKey +
      "&units=metric";
    const response = await axios.get(url);

    const weatheTemp = await response.data.main.temp;
    const weatherDescrition = await response.data.weather[0].description;
    const weatherIcon = await response.data.weather[0].icon;
    const imgUrl =
      (await "https://openweathermap.org/img/wn/") + weatherIcon + "@2x.png";
    const City = await response.data.name;

    setTemp(weatheTemp);
    setDescription(weatherDescrition);
    setIcon(imgUrl);
    setCity(City);
    setInput("");
    console.log(response)
  };

  return (
    <div className="app">
      <FlipMove>
        {temp === "" ? (
          <Input input={input} setInput={setInput} Findweather={Findweather} />
        ) : (
          <Result
            temp={temp}
            icon={icon}
            description={description}
            city={city}
            setTemp={setTemp}
          />
        )}
      </FlipMove>
    </div>
  );
};

export default App;
